@font-face {
  font-family: "ManropeL";
  src: url("../../build/fonts/Manrope-Light.ttf");
}
@font-face {
  font-family: "ManropeR";
  src: url("../../build/fonts/Manrope-Regular.ttf");
}
@font-face {
  font-family: "InterM";
  src: url("../../build/fonts/Inter-Medium.ttf");
}
@font-face {
  font-family: "InterB";
  src: url("../../build/fonts/Inter-Bold.ttf");
}
#bodyAdmin {
  background-color: rgba(0, 0, 0, 0.719);
}

.container {
  max-width: 1900px;
  height: 100%;
  margin: 0 auto;
  display: flex;
}

.settings {
  width: 20%;
  height: 100vh;
  display: flex;
  background-color: #364037;
  justify-content: center;
  overflow: scroll;
}
.settings-links {
  width: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.links-image {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.links-image a img {
  width: 6.25rem;
  height: 6.375rem;
  object-fit: cover;
  border-radius: 100%;
}
.links-image span {
  color: #fff;
  font-family: "InterB";
}

.links-link {
  display: flex;
  flex-direction: column;
}

.link {
  display: flex;
  flex-direction: column;
}
.link-title {
  color: #986c43;
  font-size: 1.3rem;
  padding: 0.5em 0;
  text-align: center;
}
.link a {
  text-decoration: none;
  color: #fff;
  font-size: 0.9rem;
  padding: 0.5em 0;
}

.link__salir {
  color: red;
  font-size: 1.5rem;
  background-color: red;
  text-align: center;
  padding: 0.5em 0;
}

.crud {
  width: 80%;
}