@use '../base/variables' as var;
#bodyAdmin{
    background-color: rgba(0, 0, 0, 0.719);
}
.container{
    max-width: 1900px;
    
    height: 100%;
    margin: 0 auto;
    display: flex;
}
.settings{
    width: 20%;
    height: 100vh;
    display: flex;
    background-color: var.$verde;
    justify-content: center;
    overflow: scroll;
    &-links{
        width: 90%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.links-image{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;

    & a img{
        width: 6.25rem;
        height: 6.375rem;
        object-fit: cover;
        border-radius: 100%;
    }

    & span{
        color: var.$blanco;
        font-family: var.$bold;
    }
}


.links-link{
    display: flex;
    flex-direction: column;
}

.link{
    display: flex;
    flex-direction: column;

    &-title{
        color: var.$naranja;
        font-size: 1.3rem;
        padding: .5em 0;
        text-align: center;
    }

    & a{
        text-decoration: none;
        color: var.$blanco;
        font-size: .9rem;
        padding: .5em 0;
        
    }
}

.link__salir{
    color: red;
    font-size: 1.5rem;
    background-color: red;
    text-align: center;
    padding: .5em 0;
}
.crud{
    width: 80%;
}